import d3 from 'd3/d3';
export const MarterBiddingFuture20DaysFakeData = () => {
    var Future20Days = [
    ]
    var formatDate = d3.time.format("%d %b");
    for (var i = 0; i < 20; i++) {
        var currentDate = new Date();
        currentDate.setDate(currentDate.getDate() + i);
        var obj = {};
        obj['numberOfSide'] = 1;
        obj['powerCapacity']=180;
        obj['period'] = formatDate(currentDate);
        Future20Days.push(obj);
    }
    return Future20Days;
}
