import React,{useEffect} from 'react';
import C3Chart from 'react-c3js';
import 'c3/c3.css';
import './style.css';
import { LineChartFakeData } from './fakeData';
import { useDispatch, useSelector } from 'react-redux';
import { marketBiddingLineChartActions } from '../../../_redux/_actions';

export const MarketBiddingLineChart = () => {
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(marketBiddingLineChartActions.getMarketBiddingLineChartData());
    }, []);
    const dataFromApi = useSelector(state => state.marketBiddingLineChartData)

    const marketBiddingLineChartData = dataFromApi ? dataFromApi : LineChartFakeData();
    const data = {
        columns: [
            ['USEP $/MWh', ...marketBiddingLineChartData.map(v => v.USEP)]
        ],
        axes: {
            'data1': 'y'
        }
    },
        axis = {
            y: {
                label: {
                    text: '$/MWh',
                    position: 'inner-middle'
                },
                tick: {
                    outer: false,
                    values: [0, 5, 10, 15, 20, 25, 30, 35, 40]
                },
                padding: {
                    bottom: 6
                }
            },
            x: {
                type: 'category',
                categories: [...marketBiddingLineChartData.map(v => v.Period)],
                label: {
                    text: 'period',
                    position: 'inner-center'
                },
                tick: {
                    outer: false,
                    multiline: false,
                    centered: true
                }
            }
        },
        grid = {
            x: {
                lines: [
                    { value: 0 }, { value: 1 }, { value: 2 }, { value: 3 }, { value: 4 }, { value: 5 },
                    { value: 6 }, { value: 7 }, { value: 8 }, { value: 9 }, { value: 10 }, { value: 11 },
                    { value: 12 }, { value: 13 }, { value: 14 }, { value: 15 }, { value: 16 }, { value: 17 },
                    { value: 18 }, { value: 19 }, { value: 20 }, { value: 21 }, { value: 22 }, { value: 23 },
                    { value: 24 }, { value: 25 }, { value: 26 }, { value: 27 }, { value: 28 }, { value: 29 },
                    { value: 30 }, { value: 31 }, { value: 32 }, { value: 33 }, { value: 34 }, { value: 35 },
                    { value: 36 }, { value: 37 }, { value: 38 }, { value: 39 }, { value: 40 }, { value: 41 },
                    { value: 42 }, { value: 43 }, { value: 44 }, { value: 45 }, { value: 46 }, { value: 47 }
                ]
            },
            y: {
                show: true
            }
        },
        legend = {
            show: false
        };
    return <C3Chart data={data} axis={axis} grid={grid} legend={legend} />;


}