import React,{useEffect} from "react";
import { UserTable } from './components';
import "./UserListPage.css";
import EvercommLogo from "../../assets/img/evercomm.jpg";

const UserListPage = props => {    
    return (
        <div>
            <div className="table_container">
                <div className="add">
                    <button
                        type="button"
                        className="btn btn-primary"
                        data-toggle="modal"
                        data-target="#add"
                    >
                        <i className="glyphicon glyphicon-plus" />
                        Add User
                    </button>
                </div>
                <UserTable/>
            </div>
            <div>
                <footer>
                    <div className="footer float-right">
                        Powered by &nbsp;&nbsp; <img src={EvercommLogo} width='auto' height="30px" alt="Evercomm" />
                    </div>
                </footer>
            </div>
        </div>
    );
}
export { UserListPage };