import React, { useState } from 'react'
import { SelectBox } from '../constantComponents/SelectBox';
import { ModalAction } from '../constantAction/ModalAction';
import { userModels } from '../../../../models/userModels/user.models';

export const AddModal = ({ getAllUser }) => {
    const [selectedRole, setSelectedRole] = useState("")
    const [addError, setaddError] = useState({
        userIdError: false,
        nameError: false,
        emailError: false,
        mobileNoError: false,
        roleError: false,
        isActiveError: false,
        passwordError: false
    })
    const [addUserData, setaddUserData] = useState({
        userId: 0,
        name: "",
        email: "",
        mobileNo: "",
        isActive: true,
        password: ""
    })
    const _handleRoleChange = value => {
        setSelectedRole(value)
    };
    const addChange = e => {
        let data = addUserData;
        let name = e.target.dataset.name;
        data[name] = e.target.value;
        setaddUserData(data)
    };
    const addUser = () => {
        const _data = addUserData;
        let data = _data;
        let errorList = addError;
        if (data.name === "") {
            errorList.nameError = true;
        } else {
            errorList.nameError = false;
        }
        if (data.email === "") {
            errorList.emailError = true;
        } else {
            errorList.emailError = false;
        }
        if (data.password === "") {
            errorList.passwordError = true;
        } else {
            errorList.passwordError = false;
        }
        if (data.mobileNo === "") {
            errorList.mobileNoError = true;
        } else {
            errorList.mobileNoError = false;
        }
        if (ModalAction._validateEmail(data.email) === false) {
            errorList.emailError = true;
        } else {
            errorList.emailError = false;
        }
        setaddError(errorList)
        if (selectedRole.value === "" && selectedRole.value === "USER") {
            errorList.roleError = true;
        } else {
            errorList.roleError = false;
            data["role"] = selectedRole.value;
        }
        if (
            ModalAction._validateEmail(data.email) === true &&
            data.password !== "" &&
            data.name !== "" &&
            data.mobileNo !== ""
        ) {
            userModels.createUser(data)
                .then(res => {
                    if (res.ok) {
                        return res.json();
                    }
                })
                .then(res => {
                    setaddUserData({
                        name: "",
                        email: "",
                        mobileNo: "",
                        role: "",
                        isActive: "",
                        password: ""
                    })
                    setSelectedRole("")
                    getAllUser();
                    document.querySelector("#addmodal").click();
                });
        }
    };
    return (
        <div className="modal fade" id="add" role="dialog">
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <button type="button" className="close" data-dismiss="modal">
                            &times;
                  </button>
                        <h4 className="modal-title" />
                    </div>
                    <div className="modal-body">
                        <p>Add user</p>
                    </div>
                    <div className="modal-body p-5">
                        <form className="form-group">
                            <label>Name</label>
                            <div>
                                <input
                                    type="text"
                                    className="form-control"
                                    placeholder="Enter Name"
                                    data-name="name"
                                    value={addUserData.name}
                                    onChange={e => addChange(e)}
                                />
                                {addError.nameError ? (
                                    <small className="error-txt">Name is required</small>
                                ) : (
                                        ""
                                    )}
                            </div>
                            <label>E-Mail</label>
                            <div>
                                <input
                                    type="email"
                                    className="form-control"
                                    placeholder="Enter Email"
                                    data-name="email"
                                    value={addUserData.email}
                                    onChange={addChange}
                                />
                                {addError.emailerror ? (
                                    <small className="error-txt">Email is required</small>
                                ) : (
                                        ""
                                    )}
                            </div>
                            <label>Password</label>
                            <div>
                                <input
                                    type="password"
                                    className="form-control"
                                    placeholder="Enter password"
                                    data-name="password"
                                    value={addUserData.password}
                                    onChange={addChange}
                                />
                                {addError.passwordError ? (
                                    <small className="error-txt">
                                        Password is required
                                    </small>
                                ) : (
                                        ""
                                    )}
                            </div>
                            <label>Mobile Number</label>
                            <div>
                                <input
                                    type="text"
                                    className="form-control"
                                    placeholder="Enter Mobile Number"
                                    data-name="mobileNo"
                                    value={addUserData.mobileNo}
                                    onChange={addChange}
                                />
                                {addError.mobileNoError ? (
                                    <small className="error-txt">
                                        Mobile Number is required
                                    </small>
                                ) : (
                                        ""
                                    )}
                            </div>
                            <label>Role</label>
                            <div>
                                <SelectBox
                                    placeholder="Select Role"
                                    handleInputChange={e => _handleRoleChange(e)}
                                    options={ModalAction.roleOptions}
                                    value={selectedRole}
                                />
                                {addError.roleError ? (
                                    <small className="error-txt">Role is required</small>
                                ) : (
                                        ""
                                    )}
                            </div>
                        </form>
                    </div>
                    <div className="modal-footer">
                        <button
                            type="button"
                            id="addmodal"
                            className="btn btn-default"
                            data-dismiss="modal"
                        >
                            Close
                  </button>
                        <button
                            type="button"
                            className="btn btn-primary"
                            onClick={addUser}
                        >
                            Add
                  </button>
                    </div>
                </div>
            </div>
        </div>
    )
}
