import React from 'react'
import { base_url } from "../../../../_redux/_constants/BaseUrl";

const SuspendModal = ({
    suspendUserData,
    setsuspendUserData,
    getAllUser
}) => {
    const suspendUser = () => {
        let data = suspendUserData;
        let loginData = JSON.parse(localStorage.getItem('loginData'));
        fetch(`${base_url}/v1/admin/suspend-user`, {
            method: "POST",
            headers: {
                "Content-type": "application/json; charset=UTF-8",
                "X-AUTH-TOKEN": loginData.data.token
            },
            body: JSON.stringify({
                userId: data.userId,
                isActive: false
            })
        })
            .then(res => {
                if (res.ok) {
                    return res.json();
                }
            })
            .then(res => {
                console.log(res);
                setsuspendUserData({})
                getAllUser()
                document.querySelector("#suspendmodal").click();
            });
    };
    return (
        <div className="modal fade" id="suspend" role="dialog">
            <div className="modal-dialog">
                {/* Modal Content */}
                <div className="modal-content">
                    <div className="modal-header">
                        <button type="button" className="close" data-dismiss="modal">
                            &times;
                  </button>
                        <h4 className="modal-title" />
                    </div>
                    <div className="modal-body">
                        <p>Suspend user ?</p>
                    </div>
                    <div className="modal-footer">
                        <button
                            type="button"
                            id="suspendmodal"
                            className="btn btn-default"
                            data-dismiss="modal"
                        >
                            Close
                  </button>
                        <button
                            type="button"
                            className="btn btn-warning"
                            onClick={suspendUser}
                        >
                            Suspend
                  </button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export { SuspendModal }
