import React, { useEffect, useState } from "react";
import { InfoWindow, Marker, Map, GoogleApiWrapper } from "google-maps-react";
import fontawesome from "fontawesome-markers";
import { map_api_key } from '../../../../../constants';
// import { useDispatch, useSelector } from 'react-redux';
import InfoWindowforMap from './InfoWindowforMap';
// import { drawMapActions } from '../../../../../_redux/_actions';
import { overViewModels } from '../../../../../models/overViewModels/overView.models';

const DrawMap = (props) => {
  // const dispatch = useDispatch();

  let realDataLatLngs = null, generatorInfo = {
    generatorId: null,
          generatorName: null,
          guage: null,
          available: null,
          standByGen_start_running_stop: null,
          standByGen_gen_running: null,
          standByGen_gen_voltage: null,
          standByGen_gen_power: null,
          standByGen_current: null,
          standByGen_gen_frequency: null,
          standByGen_fuel_status: null,
          standByGen_remu: null,
          incPanelIncVoltage: null,
          incPanelGridFrequency: null,
          incPanelInverterStatus: null,
          sysStatusMainBreaker: null,
          sysStatusRemote: null
  };

  const [showingInfoWindow, setShowingInfoWindow] = useState(false)
  const [activeMarker, setActiveMarker] = useState({})
  const [selectedPlace, setSelectedPlace] = useState({})

  useEffect(() => {
    overViewModels.getLatLngs().then(data => {
      if (data) {
        realDataLatLngs = data
      }
    })
    overViewModels.getGeneratorInfo().then(data => {
      if (data) {
        generatorInfo = {
          generatorId: data.generatorId,
          generatorName: data.generatorName,
          guage: data.guage,
          available: data.available,
          standByGen_start_running_stop: data.standby_generator_panel[0].start_running_stop,
          standByGen_gen_running: data.standby_generator_panel[0].gen_running,
          standByGen_gen_voltage: data.standby_generator_panel[0].gen_voltage,
          standByGen_gen_power: data.standby_generator_panel[0].gen_power,
          standByGen_current: data.standby_generator_panel[0].gen_current,
          standByGen_gen_frequency: data.standby_generator_panel[0].gen_frequency,
          standByGen_fuel_status: data.standby_generator_panel[0].fuel_status,
          standByGen_remu: data.standby_generator_panel[0].remu,
          incPanelIncVoltage: data.incomingPanel.incomingVoltage,
          incPanelGridFrequency: data.incomingPanel.gridFrequency,
          incPanelInverterStatus: data.incomingPanel.inverterStatus,
          sysStatusMainBreaker: data.system_status[0].main_breaker,
          sysStatusRemote: data.system_status[0].remote_byPass_maintenence
        }
      }
    })
    // dispatch(drawMapActions.getGeneratorInfo());
    // dispatch(drawMapActions.getLatLngs());
  }, []);

  const onMarkerClick = (props, marker, e) => {
    console.table(props);
    setSelectedPlace(props);
    setActiveMarker(marker);
    setShowingInfoWindow(true);
  };

  const onInfoWindowClose = () => {
    setShowingInfoWindow(false);
    setActiveMarker(null);
  };

  const onMapClicked = props => {
    if (showingInfoWindow) {
      setShowingInfoWindow(false);
      setActiveMarker(null);
    }
  };

  // const generatorInfo = useSelector(state => state.drawMap)
  let fillcolor = "", strokecolor = "";
  // const realDataLatLngs = useSelector(state => state.latlngs)
  latlngs = realDataLatLngs ? realDataLatLngs : latlngs;

  return (
    <div id="google-map-holder">
      <Map
        google={props.google}
        style={{ width: "100%", height: "100%", position: "inherit" }}
        className={"map"}
        initialCenter={{
          lat: 1.329832,
          lng: 103.816317
        }}
        zoom={12}
        zoomControl={false}
        gestureHandling={"cooperative"}
        onClick={onMapClicked}
      >
        {latlngs.map((latlng, i) => {
          if (latlng.status === "start") {
            fillcolor = "#0f0";
            strokecolor = "red";
          }
          if (latlng.status === "standbyoff") {
            fillcolor = "black";
            strokecolor = "red";
          }
          if (latlng.status === "running") {
            fillcolor = "#00ff00";
            strokecolor = "#00ff00";
          }
          if (latlng.status === "alerts") {
            fillcolor = "red";
            strokecolor = "red";
          }
          if (latlng.status === "stop") {
            fillcolor = "black";
            strokecolor = "white";
          }
          if (latlng.status === "standbyin") {
            fillcolor = "#0f0";
            strokecolor = "#f82d2a";
          }
          return (
            <Marker
              key={i}
              mapCenter="aarr"
              onClick={onMarkerClick}
              position={{ lat: latlng.lat, lng: latlng.lng }}
              name={latlng.status}
              lat={latlng.lat}
              lng={latlng.lng}
              icon={{
                path: fontawesome.CERTIFICATE,
                scale: 0.3,
                strokeWeight: 2,
                strokeColor: strokecolor,
                strokeOpacity: 1,
                fillColor: fillcolor,
                fillOpacity: 1
              }}
              title="Click for details"
            />
          );
        })}

        <InfoWindow
          marker={activeMarker}
          visible={showingInfoWindow}
          onClose={onInfoWindowClose}
        >
          <InfoWindowforMap
            selectedPlace={selectedPlace}
            generatorInfo={generatorInfo}
          />
        </InfoWindow>
      </Map>
    </div>
  )
}
export default GoogleApiWrapper(
  (props) => ({
    apiKey: map_api_key,
  }
  ))(DrawMap)

let latlngs = [
  { lat: 1.3244101532814916, lng: 103.85573468545533, status: 'offline' },
  { lat: 1.358048, lng: 103.886454, status: 'activated' },
  { lat: 1.347428, lng: 103.766932, status: 'standbyin' },
  { lat: 1.352520, lng: 103.922978, status: 'standbyoff' },
  { lat: 1.339727, lng: 103.713452, status: 'alert' }
];