export const ModalAction = {   
    _validateEmail,
    roleOptions:[
        { value: "USER", label: "USER" },
        { value: "ADMIN", label: "ADMIN" }
    ]
}

function _validateEmail (emailInput)  {
    let mailFormat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
    if (emailInput.match(mailFormat)) {
        return true;
    } else {
        return false;
    }
};

