import React, { useEffect } from 'react';
import d3 from 'd3/d3';
import { MarketBiddingLineChart } from './components/';
import './MarketBiddingPage.css';
import DateTime from '../constants/components/headerComponents/DataTime';
import Table from './containers/Table';
import Options from './containers/Options';
import GaugeChart from './components/gaugeChart';
import FuturePlannedCapacity from './components/FuturePlannedCapacity';
import { useDispatch, useSelector } from 'react-redux';
import {
    marketBiddingGeneratorListActions,
    marketBiddingTotalPowerCapacityActions,
    marketBiddingFuture20DaysActions,
} from '../../_redux/_actions';

//import Fake Data
import { MarketBiddingTotalPowerCapacityFakeData } from './fakeData/MarketBiddingTotalPowerCapacityFakeData';
import { MarketBiddingGeneratorListFakeData } from './fakeData/MarketBiddingGeneratorListFakeData';
import { MarterBiddingFuture20DaysFakeData } from './fakeData/MarterBiddingFuture20DaysFakeData';
//import Fake Data


const MarketBiddingPage = () => {
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(marketBiddingGeneratorListActions.getmarketBiddingGeneratorListData());
        dispatch(marketBiddingTotalPowerCapacityActions.getmarketBiddingTotalPowerCapacityData());
        dispatch(marketBiddingFuture20DaysActions.getmarketBiddingFuture20DaysData())
    }, []);

    const TotalPowerCapacityFromApi = useSelector(state => state.marketBiddingTotalPowerCapacity)
    const GeneratorListFromApi = useSelector(state => state.marketBiddingGeneratorList)
    const Future20DaysDataFromApi = useSelector(state => state.marketBiddingFuture20Days)


    const future20DaysData = Future20DaysDataFromApi.numberOfSide ? Future20DaysDataFromApi : MarterBiddingFuture20DaysFakeData();
    const generatorList = GeneratorListFromApi.regno ? GeneratorListFromApi : MarketBiddingGeneratorListFakeData();
    const powerData = TotalPowerCapacityFromApi.powerCapacity ? TotalPowerCapacityFromApi : MarketBiddingTotalPowerCapacityFakeData();

    return (
        <div className="row Market-bidding-page">
            <div className="col-lg-9 col-md-12">
                <div className="card lineChart">
                    <div className="card-header">Date : <DateTime /></div>
                    <div className="card-body">
                        <MarketBiddingLineChart />
                    </div>
                </div>
                <div className="card options">
                    <div className="card-body" style={{ padding: '0px 15px' }}>
                        <Options />
                    </div>
                </div>
                <div className="card table">
                    <div className="card-body">
                        <Table data={generatorList} />
                    </div>
                </div>
            </div>
            <div className="col-lg-3 col-md-12">
                <div className="card border-light gaugeChart">
                    <div className="card-header">Date : <DateTime /></div>
                    <div className="card-body" style={{ padding: 0 }}>
                        <span className="badge badge-pill badge-light" style={{ margin: '5px 0px 0px 10px', border: '1px solid' }}>
                            {powerData[0].numberOfSide}<br /><small>Number of sites</small>
                        </span>
                        <GaugeChart paddingBottom={0} powerCapacity={powerData[0].powerCapacity} />
                    </div>
                    {/* <div className="FuturePlannedCapacities"> */}
                   
                </div>
                <div className="container">
                <FuturePlannedCapacity future20DaysData={future20DaysData} />

                </div>
               
            </div>
        </div>
    )
}

export { MarketBiddingPage };