import React, { useState, useEffect } from 'react'
import { base_url } from "../../../../_redux/_constants/BaseUrl";
import { ModalAction } from '../constantAction/ModalAction';
import { SelectBox } from '../constantComponents/SelectBox';

export const EditModal = ({ getAllUser, editUserData, seteditUserData }) => {
    let loginData = JSON.parse(localStorage.getItem('loginData'));

    const [editError, seteditError] = useState({
        userIdError: false,
        nameError: false,
        emailError: false,
        mobileNoError: false,
        roleError: false,
        isActiveError: false,
        passwordError: false
    })

    const editUser = () => {
        let data = {
            userId: editUserData.userId,
            name: editUserData.name,
            email: editUserData.email,
            mobileNo: editUserData.mobileNo,
            role: editUserData.role,
        }
        let errorList = editError;
        if (data.userId === "") {
            errorList.userIdError = true;
        } else {
            errorList.userIdError = false;
        }
        if (data.name === "") {
            errorList.nameError = true;
        } else {
            errorList.name = false;
        }
        if (data.email === "") {
            errorList.emailError = true;
        } else {
            errorList.emailError = false;
        }
        if (data.password === "") {
            errorList.passwordError = true;
        } else {
            errorList.passwordError = false;
        }
        if (data.mobileNo === "") {
            errorList.mobileNoError = true;
        } else {
            errorList.mobileNoError = false;
        }
        if (ModalAction._validateEmail(data.email) === false) {
            errorList.emailError = true;
        } else {
            errorList.emailError = false;
        }
        if (data.role.value === "" && data.role.value === "USER") {
            errorList.roleError = true;
        } else {
            errorList.roleError = false;
            data["role"] = editUserData.role.value;
        }
        seteditError(errorList)
        if (
            ModalAction._validateEmail(data.email) === true &&
            data.password !== "" &&
            data.name !== "" &&
            data.mobileNo !== "" &&
            data.userId !== ""
        ) {
            fetch(`${base_url}/v1/admin/save-user`, {
                method: "POST",
                headers: {
                    "Content-type": "application/json; charset=UTF-8",
                    "X-AUTH-TOKEN": loginData.data.token
                },
                body: JSON.stringify(data)
            })
                .then(res => {
                    if (res.ok) {
                        return res.json();
                    }
                })
                .then(res => {
                    seteditUserData({})
                    getAllUser()
                    document.querySelector("#editmodal").click()
                });
        }
    };

    const _handleRoleChange = e => {
        let userData = editUserData;
        userData["role"] = e;
        seteditUserData(userData)
    };
    const editChange = e => {
        let data = editUserData;
        let name = e.target.dataset.name;
        data[name] = e.target.value;
        seteditUserData(data)
    };

    useEffect(() => {
        let editData = editUserData;
        const roledata = { label: `${editUserData.role}`, value: `${editUserData.role}` }
        editData.role = roledata;
        seteditUserData(editData);
    }, [editUserData])

    return (
        <div className="modal fade" id="edit" role="dialog">
            <div className="modal-dialog">
                {/* Modal Content */}
                <div className="modal-content">
                    <div className="modal-header">
                        <button type="button" className="close" data-dismiss="modal">
                            &times;
                  </button>
                        <h4 className="modal-title" />
                    </div>
                    <div className="modal-body">
                        <p>Edit user</p>
                    </div>
                    <div className="modal-body p-5">
                        <form className="form-group">
                            <label>User ID</label>
                            <div>
                                <input
                                    type="number"
                                    className="form-control"
                                    placeholder="Enter User ID"
                                    data-name="userId"
                                    value={editUserData.userId}
                                    onChange={editChange}
                                />
                                {editError.userIdError ? (
                                    <small className="error-txt">User ID is required</small>
                                ) : (
                                        ""
                                    )}
                            </div>
                            <label>Name</label>
                            <div>
                                <input
                                    type="text"
                                    className="form-control"
                                    placeholder="Enter Name"
                                    data-name="name"
                                    value={editUserData.name}
                                    onChange={editChange}
                                />
                                {editError.nameError ? (
                                    <small className="error-txt">Name is required</small>
                                ) : (
                                        ""
                                    )}
                            </div>
                            <label>E-Mail</label>
                            <div>
                                <input
                                    type="email"
                                    className="form-control"
                                    placeholder="Enter Email"
                                    data-name="email"
                                    value={editUserData.email}
                                    onChange={editChange}
                                />
                                {editError.emailerror ? (
                                    <small className="error-txt">Email is required</small>
                                ) : (
                                        ""
                                    )}
                            </div>
                            <label>Password</label>
                            <div>
                                <input
                                    type="password"
                                    className="form-control"
                                    placeholder="Enter password"
                                    data-name="password"
                                    value={editUserData.password}
                                    onChange={editChange}
                                />
                                {editError.passwordError ? (
                                    <small className="error-txt">
                                        Password is required
                                    </small>
                                ) : (
                                        ""
                                    )}
                            </div>
                            <label>Mobile Number</label>
                            <div>
                                <input
                                    type="text"
                                    className="form-control"
                                    placeholder="Enter Mobile Number"
                                    data-name="mobileNo"
                                    value={editUserData.mobileNo}
                                    onChange={editChange}
                                />
                                {editError.mobileNoError ? (
                                    <small className="error-txt">
                                        Mobile Number is required
                                    </small>
                                ) : (
                                        ""
                                    )}
                            </div>
                            <label>Role</label>
                            <div>
                                <SelectBox
                                    placeholder="Select Role"
                                    handleInputChange={e => _handleRoleChange(e)}
                                    options={ModalAction.roleOptions.map(v => ({ ...v, value: v.value, label: v.label }))}
                                    value={editUserData.role}
                                />
                                {editError.roleError ? (
                                    <small className="error-txt">Role is required</small>
                                ) : (
                                        ""
                                    )}
                            </div>
                        </form>
                    </div>
                    <div className="modal-footer">
                        <button
                            type="button"
                            id="editmodal"
                            className="btn btn-default"
                            data-dismiss="modal"
                        >
                            Close
                  </button>
                        <button
                            type="button"
                            className="btn btn-primary"
                            onClick={editUser}
                        >
                            Edit
                  </button>
                    </div>
                </div>
            </div>
        </div>
    )
}
