import React, { Component } from 'react'
import Select from 'react-select'

export const SelectBox = ({ placeholder, options, value, handleInputChange }) => {
    return (
        <div>
            <Select
                options={options}
                placeholder={placeholder || "Select"}
                value={value}
                data-name="role"
                onChange={handleInputChange}
            />
        </div>
    )


}

