export const OverviewPageOnedataChartFakeData = () => {
    
    var OverviewPageOnedataChartData = [
    ]

    for (var i = 0; i < 47; i++) {
        var obj = {};
        obj['USEP'] = Math.floor(Math.random() * 40) + 1;
        obj['Period'] = i+1;
        OverviewPageOnedataChartData.push(obj);
    }
    return OverviewPageOnedataChartData;
}
