import React, { useEffect } from 'react'
import {userModels} from '../../../models/userModels/user.models';

const GetAllUser = (setsuspendUserData, setactivateUserData, seteditUserData, setdeleteUserID, setuserInfo,userInfo) => {
    
    userModels.getAllUser().then(users=>{
        let userArray = [];
            for (let i = 0; i < users.length; i++) {
                let status = null;
                let suspendActive = null;
                if (users[i].isActive) {
                    status = "Active";
                    suspendActive = (
                        <button
                            type="button"
                            className="btn btn-warning btn-sm"
                            data-toggle="modal"
                            data-target="#suspend"
                            onClick={() => setsuspendUserData(users[i])}
                        >
                            Suspend
                        </button>
                    );
                } else {
                    status = "Inactive";
                    suspendActive = (
                        <button
                            type="button"
                            className="btn btn-warning btn-sm"
                            data-toggle="modal"
                            data-target="#activate"
                            onClick={() =>
                                setactivateUserData(users[i])
                            }
                        >
                            Activate
                        </button>
                    );
                }
                userArray.push(
                    {
                        userId: users[i].userId,
                        name: users[i].name,
                        email: users[i].email,
                        mobileNo: users[i].mobileNo,
                        role:users[i].role,
                        isActive: status,
                        editUser: (
                            <button
                                type="button"
                                className="btn btn-primary btn-sm"
                                onClick={() => {
                                    seteditUserData(users[i])
                                }}
                                data-toggle="modal"
                                data-target="#edit"
                            >
                                Edit
                            </button>
                        ),
                        suspendUser: suspendActive,
                        deleteUser: (
                            <button
                                type="button"
                                className="btn btn-danger btn-sm"
                                data-toggle="modal"
                                data-target="#delete"
                                onClick={() =>
                                    setdeleteUserID(users[i].userId)
                                }
                            >
                                Delete
                            </button>
                        )
                    }
                );
            }
            setuserInfo(userArray)
    })
}

export { GetAllUser }
