import React, { Component } from 'react';
import './style.css';
const $ = require('jquery');


const options = () => {

    const ChangedCheckBoxForOffline = () => {
        var checkBoxes = $("#datatableForGeneratorBiddingPanel input[name='offered-filter-ck'][value='1']");
        if ($("#checkBoxforOffered").is(":checked")) {
            checkBoxes.prop('checked', true);
        }
        else {
            checkBoxes.prop('checked', false);
        }
    }

    const ChangedCheckBoxForOffered = () => {
        var checkBoxes = $("#datatableForGeneratorBiddingPanel input[name='offline-filter-ck'][value='1']");
        if ($("#checkBoxforOffline").is(":checked")) {
            checkBoxes.prop('checked', true);
        }
        else {
            checkBoxes.prop('checked', false);
        }
    }
    return (

        <div>
            <div className="row">
                <div className="col-lg-4 col-md-6">
                    <div className="btn-group btn-block">
                        <button type="button" className="btn  btn-block btn-secondary" style={{ fontSize: '12px' }}>Date</button>
                        <input type="date" style={{ width: '100%' }} className="" />
                    </div>
                    <div className="btn-group btn-block">
                        <button type="button" className="btn  btn-block" style={{ marginTop: '0',fontSize: '12px' }} disabled="disabled">Offline:2 MW</button>
                        <button type="button" className="btn  btn-block" style={{ marginTop: '0',fontSize: '12px' }} disabled="disabled">Offered:1.8 MW</button>
                    </div>
                </div>
                <div className="col-lg-2 col-md-6">
                    <label className="btn  btn-block btn-secondary " htmlFor="my-file-selector" style={{ padding: '1vh',fontSize:'12px' }}>
                        <input id="my-file-selector" type="file" style={{ display: 'none',fontSize: '12px' }}  />
                        Load File
                    </label>

                    <button type="button" className="btn btn-block " disabled style={{ fontSize: '12px' }}>Offline: 2 MW</button>
                </div>
                <div className="col-lg-2 col-md-6">
                    <button type="button" className="btn  btn-block btn-secondary" style={{ fontSize: '12px' }}>Save/Save As</button>
                    <button type="button" className="btn btn-block " disabled style={{ fontSize: '12px' }}>Offered:1.8MW</button>
                </div>
                <div className="col-lg-2 col-md-6">
                    <button type="button" className="btn  btn-block btn-success" style={{ fontSize: '12px' }}>UPDATE</button>
                    <button type="button" className="btn btn-block  btn-warning" style={{ fontSize: '12px' }}>SUBMIT</button></div>
                <div className="col-lg-2 btn-group justify-content-center">
                    <div className=" form-group">
                        <label htmlFor="checkBoxforOffered" style={{ textAlign: 'center', paddingRight: '5px', float: 'left', fontSize: '12px' }}>Offered</label>
                        <input type="checkbox" className="form-control" id="checkBoxforOffered" onChange={ChangedCheckBoxForOffline} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="checkBoxforOffline" style={{ textAlign: 'center', paddingLeft: '5px', float: 'left', fontSize: '12px' }}>Offline</label>
                        <input type="checkbox" className="form-control" id="checkBoxforOffline" onChange={ChangedCheckBoxForOffered} />
                    </div>
                </div>

            </div>
            {/* <div className="">
                <button type="button" className="btn btn-block  btn-warning">SUBMIT</button>
            </div>


            <button type="button" className="btn  btn-block" style={{ marginTop: '0' }} disabled="disabled">Offline:2 MW</button>
            <button type="button" className="btn  btn-block" style={{ marginTop: '0' }} disabled="disabled">Offered:1.8 MW</button>






            <div className="">
                <button type="button" className="btn btn-block " disabled>Offline: 2 MW</button>
            </div>
            <div className="">
                <button type="button" className="btn btn-block " disabled>Offered: 1.8 MW</button>
            </div>


             */}

        </div>
    )
}

export default options;