import React,{useEffect} from 'react';
import { GeneratorInfoLeftSide } from './components/GeneratorInfoLeftSide';
import { SolarImageRightSide } from './components/SolarImageRightSide';
import './SiteControlPage.css';
import { useDispatch, useSelector } from 'react-redux';
import {
  siteControlSolarImageActions,
} from '../../_redux/_actions';

import img1 from '../../assets/img/solar1.jpeg';
import img2 from '../../assets/img/solar2.jpeg';
import img3 from '../../assets/img/solar3.jpeg';
import img4 from '../../assets/img/solar4.png';

const SiteControlPage = () => {
  const imgUrl = [
    { url: img1 },
    { url: img2 },
    { url: img3 },
    { url: img4 }
  ]

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(siteControlSolarImageActions.getsiteControlSolarImageData());
  }, []);

  const SolarImageUrl = useSelector(state => state.siteContorlSolarImage)

  return (
    <div className="row SiteControlPage">
      <div className="col-md-3">
        <GeneratorInfoLeftSide />
      </div>
      <div className="col-md-9">
        <SolarImageRightSide imgUrl={imgUrl} />
      </div>
    </div>
  )
}

export { SiteControlPage };