import React from 'react';
import { Header } from '../constants/components/Header';
import Map from "./components/map/Map";
import DateTime from '../constants/components/headerComponents/DataTime';
import './Overview.css';
import RealTimePricesAndDemandChart from './components/Chart/RealTimePricesAndDemand/RealTimePricesAndDemand';
import SevenDayPricesAndDemandChart from './components/Chart/7DayPricesAndDemand/SevenDayPricesAndDemand';
import RealTimeAncillaryServicesPrices from './components/Chart/RealTimeAncillaryServicesPrices/RealTimeAncillaryServicesPrices';
import OverviewPageOneDataChart from './components/Chart/OverviewPageOnedataChart/OverviewPageOnedataChart';
import EvercommLogo from '../../assets/img/evercomm.jpg';

const OverView = () => {
  return (
    <div className="row OverviewPage" >
      <div className="col-lg-3 col-md-3 " >
        <div className="card mb-1 realTimePriceAndDemand">
          <div className="card-body">
            <h5 className="card-title">
              Real-Time Prices and Demand- <DateTime page="overview" />
            </h5>
            <RealTimePricesAndDemandChart />
          </div>
        </div>
        <div className="card mb-1 sevenDayPricesAndDemand">
          <div className="card-body">
            <h5 className="card-title">7 Day Prices and Demand</h5>
            <SevenDayPricesAndDemandChart />
          </div>
        </div>
        <div className="card mb-1 realTimeAncillaryServicesPrices">
          <div className="card-body">
            <h5 className="card-title">
              Real-Time Ancillary Services Prices-{" "}
              <DateTime page="overview" />
            </h5>
            <RealTimeAncillaryServicesPrices />
          </div>
        </div>
      </div>

      <div className="col-lg-9 col-md-9">
        <div>
          <Map />
        </div>
        <div className="card OverviewPageOnedataChart">
          <div className="card-body">
            <OverviewPageOneDataChart />
          </div>
        </div>
      </div>
      <div>&nbsp;</div>
      <div>&nbsp;</div>
      <div>
        <footer>
          <div className="footer float-right">
            Powered by &nbsp;&nbsp; <img src={EvercommLogo} width='auto' height="30px" alt="Evercomm" />
          </div>
        </footer>
      </div>
    </div>
  );
}


export { OverView }