import React, { Component } from 'react';
import GaugeChart from './gaugeChart';

const FlexItem=(props)=> {
        return (
            <div className="col-sm-4 col-md-3 col-lg-4 flexItem" style={{ height: 115 }}>
                <div className="flexItemGaugeChart">
                    <span className="badge badge-pill badge-light"
                        style={{ border: '1px solid', fontSize: 10, marginTop: 1, width: '100%' }}>
                        {props.value.numberOfSide}
                        <br />
                        <small>Number of sites</small>
                    </span>
                    <div>
                        <GaugeChart paddingBottom={100} powerCapacity={props.value.powerCapacity} />
                    </div>
                </div>
                <div className="flexItemDate" style={{ marginTop: -100 }}>
                    {props.value.period}
                </div>
            </div>
        )
}

export default FlexItem;