import React, { useEffect } from 'react';
import C3Chart from 'react-c3js';
import 'c3/c3.css';
import './RealTimePricesAndDemand.css';
import { RealTimePricesAndDemandFakeData } from './RealTimePricesAndDemandFakeData';
import { useDispatch, useSelector } from 'react-redux';
import { realTimePriceAndDemandActions } from '../../../../../_redux/_actions';

const Chart = () => {
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(realTimePriceAndDemandActions.getPriceAndDemand());
    }, []);
    const dataFromApi = useSelector(state => state.realTimePriceAndDemandData)
    const RealTimePricesAndDemandData = dataFromApi ? dataFromApi : RealTimePricesAndDemandFakeData();
    const data = {
        columns: [
            ['Demand', ...RealTimePricesAndDemandData.map(v => v.Demand)],
            ['USEP', ...RealTimePricesAndDemandData.map(v => v.USEP)],
            ['BVP', ...RealTimePricesAndDemandData.map(v => v.BVP)],
            ['LVP', ...RealTimePricesAndDemandData.map(v => v.LVP)]
        ],
        types: {
            Demand: 'bar'
        },
        axes: {
            USEP: 'y',
            Demand: 'y2'
        },
        colors: {
            Demand: '#2159b8',
            USEP: '#ff4040',
            BVP: '#9b0000',
            LVP: '#00ffff'
        },
        type: 'spline'
    },
        axis = {
            y: {
                max: 200,
                min: 0,
                label: {
                    text: 'USEP ($/MWh)',
                    position: 'outer-middle'
                },
                tick: {
                    outer: false,
                    values: [0, 50, 100, 150]
                },
                padding: {
                    bottom: 0
                }
            },
            y2: {
                min: 0,
                max: 6000,
                show: 'true',
                label: {
                    text: 'Demand (MW)',
                    position: 'outer-middle'
                },
                tick: {
                    outer: false,
                    values: [0, 2000, 4000, 6000]
                },
                padding: {
                    bottom: 0
                }
            },
            x: {
                type: 'category',
                categories: [...RealTimePricesAndDemandData.map(v => v.Period)],
                label: {
                    text: 'Period',
                    position: 'outer-center'
                },
                tick: {
                    outer: false,
                    multiline: false,
                    centered: true
                },
                height: 40,
                padding: {
                    bottom: 10
                }
            }
        },
        grid = {
            y: {
                show: true
            }
        },
        padding = {
            top: 0
        },
        size = {
            height: 170
        },
        regions = [
            { axis: 'x', end: 10, class: 'regionX' }
        ]
        ;
    return <C3Chart class={"c4"} data={data} axis={axis} grid={grid} padding={padding} regions={regions} />;

}

export default Chart;