import React, { useState, useEffect } from 'react';
import DataTable, { createTheme, defaultThemes } from 'react-data-table-component';
import { GetAllUser } from './GetAllUser';
import { columns, TextField, customStyles } from './constantAction/UserTableColumns';
import { AllModals } from "./AllModals";

createTheme('solarized', {
    text: {
        primary: '#212529',
        secondary: '#2aa198',
    },
    background: {
        default: '#E9E9EF',
    },
    context: {
        background: '#E9E9EF',
        text: '#FFFFFF',
    },
    sortFocus: {
        default: '#2aa198',
    },
});

const FilterComponent = ({ filterText, onFilter, onClear }) => (
    <>
        <TextField id="search" type="text" placeholder="Search" value={filterText} onChange={onFilter} />
        {/* <ClearButton type="button" onClick={onClear}>X</ClearButton> */}
    </>
);

const UserTable = props => {

    const [suspendUserData, setsuspendUserData] = useState({
        userId: Number,
        isActive: false
    })
    const [activateUserData, setactivateUserData] = useState({
        userId: Number,
        isActive: true
    })

    const [role, setrole] = useState(null)
    const [editUserData, seteditUserData] = useState({})
    const [deleteUserID, setdeleteUserID] = useState(0)
    const [userInfo, setuserInfo] = useState([])
    const [filterText, setFilterText] = React.useState('');
    const [resetPaginationToggle, setResetPaginationToggle] = React.useState(false);
    useEffect(() => {
        getAllUser();
    });
    const getAllUser = () => {
        // if (users !== undefined) {
            GetAllUser(setsuspendUserData, setactivateUserData, seteditUserData, setdeleteUserID, setuserInfo, setrole)
        // }
    }
    const filteredItems = userInfo.filter(item => item.name && item.name.toLowerCase().includes(filterText.toLowerCase()) ||
        item.mobileNo && item.mobileNo.toLowerCase().includes(filterText.toLowerCase()));
    const subHeaderComponentMemo = React.useMemo(() => {
        const handleClear = () => {
            if (filterText) {
                setResetPaginationToggle(!resetPaginationToggle);
                setFilterText('');
            }
        };

        return <FilterComponent onFilter={e => setFilterText(e.target.value)} onClear={handleClear} filterText={filterText} />;
    }, [filterText, resetPaginationToggle]);

    return (
        <div>
            <DataTable
                title="USER LIST"
                columns={columns}
                data={filteredItems}
                pagination
                subHeader
                subHeaderComponent={subHeaderComponentMemo}
                subHeaderAlign={'left'}
                paginationRowsPerPageOptions={[5, 20, 50]}
                theme="solarized"
                customStyles={customStyles}
            />
            <AllModals
                suspendUserData={suspendUserData}
                setsuspendUserData={setsuspendUserData}
                getAllUser={getAllUser}
                setactivateUserData={setactivateUserData}
                activateUserData={activateUserData}
                deleteUserID={deleteUserID}
                editUserData={editUserData}
                seteditUserData={seteditUserData}
                setrole={setrole}
                role={role}
            />
        </div>
    );
}
export { UserTable }
