import React from 'react';
import './FuturePlannedCapacity.css';
import FlexItem from './FuturePlannedCapacityFlexItem';

const FuturePlan = (props) => {
    var future20DaysData = props.future20DaysData;

    return (
            <div className="row ">
                {future20DaysData.map((value, index) => {
                    return <FlexItem key={index} value={value} />;
                })}
            </div>
      

    )
}

export default FuturePlan;