import React,{useEffect} from 'react';
import C3Chart from 'react-c3js';
import 'c3/c3.css';
import './OverviewPageOnedataChart.css';
import { OverviewPageOnedataChartFakeData } from './OverviewPageOnedataChartFakeData';
import { useDispatch, useSelector } from 'react-redux';
import { overviewPageOnedataChartActions } from '../../../../../_redux/_actions';

const Chart = () => {
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(overviewPageOnedataChartActions.getOverviewPageOnedata());
    }, []);
    const dataFromApi = useSelector(state => state.OverviewPageOnedataChartData)

    const OverviewPageOnedataChartData=dataFromApi?dataFromApi:OverviewPageOnedataChartFakeData();
    
    const data = {
        columns: [
            ['USEP $/MWh', ...OverviewPageOnedataChartData.map(v => v.USEP)]
        ],
        axes: {
            'USEP $/MWh': 'y'
        }
    },
        axis = {
            y: {
                label: {
                    text: '$/MWh',
                },
                tick: {
                    outer: false,
                    values: [0, 20, 40]
                },
                padding: {
                    bottom: 7
                }
            },
            x: {
                type: 'category',
                categories: [...OverviewPageOnedataChartData.map(v => v.Period)],
                label: {
                    text: 'period',
                    // position : 'outer-center'
                },
                tick: {
                    outer: false,
                    multiline: false,
                    centered: true
                }
            }
        },
        grid = {
            x: {
                lines: [
                    { value: 0 }, { value: 1 }, { value: 2 }, { value: 3 }, { value: 4 }, { value: 5 },
                    { value: 6 }, { value: 7 }, { value: 8 }, { value: 9 }, { value: 10 }, { value: 11 },
                    { value: 12 }, { value: 13 }, { value: 14 }, { value: 15 }, { value: 16 }, { value: 17 },
                    { value: 18 }, { value: 19 }, { value: 20 }, { value: 21 }, { value: 22 }, { value: 23 },
                    { value: 24 }, { value: 25 }, { value: 26 }, { value: 27 }, { value: 28 }, { value: 29 },
                    { value: 30 }, { value: 31 }, { value: 32 }, { value: 33 }, { value: 34 }, { value: 35 },
                    { value: 36 }, { value: 37 }, { value: 38 }, { value: 39 }, { value: 40 }, { value: 41 },
                    { value: 42 }, { value: 43 }, { value: 44 }, { value: 45 }, { value: 46 }, { value: 47 }
                ]
            },
            y: {
                lines: [
                    { value: 20 }, { value: 40 }
                ]
            }
        },
        // size ={
        //     height : 135
        // },
        legend = {
            show: false
        };
    return <C3Chart data={data} axis={axis} grid={grid} legend={legend} />;

}

export default Chart;