import React from 'react'
import { base_url } from "../../../../_redux/_constants/BaseUrl";

const ActivateModal = ({ getAllUser, setactivateUser,activateUserData }) => {
    const activateUser = () => {
        let data = activateUserData;
        let loginData = JSON.parse(localStorage.getItem('loginData'));
        fetch(`${base_url}/v1/admin/suspend-user`, {
            method: "POST",
            headers: {
                "Content-type": "application/json; charset=UTF-8",
                "X-AUTH-TOKEN": loginData.data.token
            },
            body: JSON.stringify({
                userId: data.userId,
                isActive: true
            })
        })
            .then(res => {
                console.log(res);
                if (res.ok) {
                    return res.json;
                }
            })
            .then(res => {
                console.log(res);
                setactivateUser({})
                getAllUser()
                document.querySelector("#activatemodal").click();
            });
    };
    return (
        <div>
            <div className="modal fade" id="activate" role="dialog">
                <div className="modal-dialog">
                    {/* Modal Content */}
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" data-dismiss="modal">
                                &times;
                  </button>
                            <h4 className="modal-title" />
                        </div>
                        <div className="modal-body">
                            <p>Activate user ?</p>
                        </div>
                        <div className="modal-footer">
                            <button
                                type="button"
                                id="activatemodal"
                                className="btn btn-default"
                                data-dismiss="modal"
                            >
                                Close
                  </button>
                            <button
                                type="button"
                                className="btn btn-primary"
                                onClick={activateUser}
                            >
                                Activate
                  </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export { ActivateModal }
