import React,{useEffect} from 'react';
import C3Chart from 'react-c3js';
import 'c3/c3.css';
import './RealTimeAncillaryServicesPrices.css';
import { RealTimeAncillaryServicesPricesFakeData } from './RealTimeAncillaryServicesPricesFakeData';
import { useDispatch, useSelector } from 'react-redux';
import { realTimeAncillaryServicesPricesActions } from '../../../../../_redux/_actions';

const Chart = () => {
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(realTimeAncillaryServicesPricesActions.getServicesPrices());
    }, []);
    const dataFromApi = useSelector(state => state.realTimeAncillaryServicesPricesData)

    const RealTimeAncillaryServicesPricesData = dataFromApi?dataFromApi:RealTimeAncillaryServicesPricesFakeData();
    const data = {
        columns: [
            ['Contingency Reserve', ...RealTimeAncillaryServicesPricesData.map(v => v.contingency_reserve)],
            ['Regulation', ...RealTimeAncillaryServicesPricesData.map(v => v.regulation_price)]
        ],
        types: {
            Demand: 'bar'
        },
        axes: {
            'Contingency Reserve': 'y',
            'Regulation': 'y2'
        },
        colors: {
            'Contingency Reserve': '#2159b8',
            'Regulation': '#ff4040'

        },
        type: 'spline'
    },
        axis = {
            y: {
                max: 50,
                min: 0,
                label: {
                    text: 'Reserve Price ($/MWh)',
                    position: 'outer-middle'
                },
                tick: {
                    outer: false,
                    values: [0, 10, 20, 30, 40]
                },
                padding: {
                    bottom: 0
                }
            },
            y2: {
                min: 0,
                max: 50,
                show: 'true',
                label: {
                    text: 'Regulation Price($/MWh)',
                    position: 'outer-middle'
                },
                tick: {
                    outer: false,
                    values: [0, 10, 20, 30]
                },
                padding: {
                    bottom: 0
                }
            },
            x: {
                type: 'category',
                categories: [...RealTimeAncillaryServicesPricesData.map(v => v.Period)],
                label: {
                    text: 'Period',
                    position: 'outer-center'
                },
                tick: {
                    outer: false,
                    multiline: false,
                    centered: true
                },
                height: 40
            }
        },
        grid = {
            y: {
                show: true
            }
        },
        padding = {
            top: 0
        },
        //   size = {
        //       height : 170
        //   },
        regions = [
            { axis: 'x', end: 10, class: 'regionX' }
        ]
        ;
    return <C3Chart class={"c4"} data={data} axis={axis} grid={grid} padding={padding} regions={regions} />;

}

export default Chart;