import styled from 'styled-components';
import {  defaultThemes } from 'react-data-table-component';
export const columns = [
    {
        name: "User ID", selector: "userId", sortable: true,
    },
    {
        name: "Name", selector: "name", sortable: true, grow: 2,
    },
    {
        name: "Email", selector: "email", sortable: true, grow: 2,
    },
    {
        name: "Mobile No.", selector: "mobileNo", sortable: true, grow: 2,
    },
    {
        name: "Role", selector: "role", sortable: true,
    },
    {
        name: "Status", selector: "isActive", sortable: true,
    },
    {
        name: "", selector: "editUser", sortable: true,
    },
    {
        name: "", selector: "suspendUser", sortable: true,
    },
    {
        name: "", selector: "deleteUser", sortable: true,
    }
];

export const TextField = styled.input`
height: 32px;
width: 200px;
border-radius: 3px;
border-top-left-radius: 5px;
border-bottom-left-radius: 5px;
border-top-right-radius: 5px;
border-bottom-right-radius: 5px;
border: 1px solid #e5e5e5;
padding: 0 32px 0 16px;
&:hover {
  cursor: pointer;
}
`;

export const customStyles = {
    header: {
        style: {
            minHeight: '56px',
        },
    },
    headRow: {
        style: {
            borderTopStyle: 'solid',
            borderTopWidth: '1px',
            borderTopColor: defaultThemes.default.divider.default,

        },
    },
    headCells: {
        style: {
            '&:not(:last-of-type)': {
                borderRightStyle: 'solid',
                borderRightWidth: '1px',
                borderRightColor: defaultThemes.default.divider.default,
                fontSize: '16px'
            },
        },
    },
    cells: {
        style: {
            '&:not(:last-of-type)': {
                borderRightStyle: 'solid',
                borderRightWidth: '1px',
                borderRightColor: defaultThemes.default.divider.default,
                fontSize: '14px'
            },
        },
    },
};
