import React from 'react'
import { base_url } from "../../../../_redux/_constants/BaseUrl";

export const DeleteModal = ({getAllUser,deleteUserID}) => {
    const deleteUser = () => {
        let loginData = JSON.parse(localStorage.getItem('loginData'));
        fetch(`${base_url}/v1/admin/delete-user/${deleteUserID}`, {
            method: "DELETE",
            headers: {
                "Content-type": "application/json; charset=UTF-8",
                "X-AUTH-TOKEN": loginData.data.token
            }
        })
            .then(res => {
                if (res.ok) {
                    return res.json();
                }
            })
            .then(res => {
                document.querySelector("#deletemodal").click();
                getAllUser();
            });
    };
    return (
        <div className="modal fade" id="delete" role="dialog">
            <div className="modal-dialog">
                {/* Modal Content */}
                <div className="modal-content">
                    <div className="modal-header">
                        <button type="button" className="close" data-dismiss="modal">
                            &times;
                        </button>
                        <h4 className="modal-title" />
                    </div>
                    <div className="modal-body">
                        <p>Delete user ?</p>
                    </div>
                    <div className="modal-footer">
                        <button
                            type="button"
                            id="deletemodal"
                            className="btn btn-default"
                            data-dismiss="modal"
                        >
                            Close
                        </button>
                        <button
                            type="button"
                            className="btn btn-danger"
                            onClick={deleteUser}
                        >
                            Delete
                        </button>
                    </div>
                </div>
            </div>
        </div>
    )
}
