import React,{useState} from 'react'
import { SuspendModal, ActivateModal, DeleteModal, EditModal, AddModal } from './usermodals'

export const AllModals = ({
    suspendUserData,setsuspendUserData,getAllUser,setactivateUserData,activateUserData,deleteUserID,
    editUserData,seteditUserData,setrole,role
}) => {
    const [addUserData, setaddUserData] = useState({
        userId: 0,
        name: "",
        email: "",
        mobileNo: "",
        isActive: true,
        password: ""
    })
    const [addError, setaddError] = useState({
        userIdError: false,
        nameError: false,
        emailError: false,
        mobileNoError: false,
        roleError: false,
        isActiveError: false,
        passwordError: false
    })
    const [editError, seteditError] = useState({
        userIdError: false,
        nameError: false,
        emailError: false,
        mobileNoError: false,
        roleError: false,
        isActiveError: false,
        passwordError: false
    })
    return (
        <div>
            <SuspendModal
                suspendUserData={suspendUserData}
                setsuspendUserData={setsuspendUserData}
                getAllUser={getAllUser}
            />
            <ActivateModal
                setactivateUser={setactivateUserData}
                getAllUser={getAllUser}
                activateUserData={activateUserData}
            />
            <DeleteModal
                getAllUser={getAllUser}
                deleteUserID={deleteUserID}
            />
            <EditModal
                editUserData={editUserData}
                seteditUserData={seteditUserData}
                editError={editError}
                setrole={setrole}
                role={role}
                seteditError={seteditError}
                getAllUser={getAllUser}
            />
            <AddModal
                addUserData={addUserData}
                setaddUserData={setaddUserData}
                getAllUser={getAllUser}
                role={role}
                setrole={setrole}
                addError={addError}
                setaddError={setaddError}
            />
        </div>
    )
}
